import slide from 'components/slide/index'
import http from 'js/http'
import chooseNode from 'views/home/vote/chooseNode'
import validPayPsw from 'components/validPayPsw/index'
import {mapState, mapMutations, mapGetters} from 'vuex'
import {GetUrlParam} from 'js/mixin'

export default {
  name: "index",
  components: {
    slide,
    chooseNode,
    validPayPsw
  },
  data() {
    return {
      typeList: [],
      number: '',
      currentType: 1,
      typeInfo: {},
      nodeName: '',
      nodeId: '',
      chooseNodeShow: false,
      validPswShow: false,
      currentTypeName:''
    }
  },
  methods: {
    clickAmount(value, cb) {
      if (!value) {
        cb('请输入投票数量')
        return
      }
      if (!(/^[1-9]\d*$/.test(value))) {
        cb('请输入有效的投票数量')
        return
      }
      if (value - this.typeInfo.number > 0) {
        cb('超出最大可投票数')
        return
      }
      // if (value - this.typeInfo.surplusNumber > 0) {
      //   cb(`${this.currentTypeName}每个用户本次竞选活动累计不超过${this.typeInfo.surplusNumber}`)
      //   return
      // }
      cb('')
    },
    sbmVote() {
      if (!this.nodeId) {
        this.$vux.toast.show('请选择投票节点')
        return
      }
      this.clickAmount(this.number, (res) => {
        if (res) {
          this.$vux.toast.show(res)
          return
        }
        this.validPswShow = true
      })
    },
    validPswSuccess(payPsw) {
      http.post('/vote/submit', {
        node_id: this.nodeId,
        type: this.currentType,
        number: this.number,
        pass: payPsw
      }, (res) => {
        let type = res.code === 0 ? 'success' : 'warn'
        this.$vux.toast.show({
          text: res.msg,
          toast: 3000,
          type: type
        })
        if (res.code === 0) {
          this.$router.go(-1)
        }
        this.validPswShow = false
      })
    },
    getTypeList() {
      http.post('/vote/types', {}, (res) => {
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.typeList = res.content
        this.currentType = this.typeList[0].id
        this.getTypeInfo()
      })
    },
    selectedNode(item) {
      this.nodeName = item.name
      this.nodeId = item.id
    },
    changeType(item) {
      this.currentType = item.id
      this.currentTypeName = item.name
      this.getTypeInfo()
    },
    getTypeInfo() {
      http.post('/vote/type-info', {
        type: this.currentType
      }, (res) => {
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.typeInfo = res.content
      })
    },
    pageInt() {
      if (!this.typeList.length) {
        this.getTypeList()
      }
      this.nodeId = this.$route.query.nodeId
      this.nodeName = this.$route.query.nodeName

    }
  },
  created() {
    this.pageInt()
  },
  activated() {
    // this.pageInt()
  },
  computed: {
    ...mapGetters([
      "loginMsg",
    ]),
  },
}



